const Table = (props) => {
    console.log("Cerner:", props.Cerner);

    return props.Cerner.entry.map((entry, index) => {
        console.log("Entry:", entry);
        switch (entry.resource.resourceType) {
            case "Observation":
            case "MedicationStatement":
            case "AllergyIntolerance":
            case "Patient":
            case "RelatedPerson":
                return <div dangerouslySetInnerHTML={{ __html: entry.resource.text.div}}></div>
            default:
                return
        }
    })
}

export default Table;